@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0, 0, 0, .8);
        transition: 0.6s all;
        &_active {
            left: 0; }
        &_item {
            padding-right: 0;
            border: none;
            margin: 10px 0; }
        &_link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 0px;
        margin-right: 10px;
        top: 34%;
        transform: translateX(-50%);
        width: 30px;
        height: 17px;
        span {
            display: block;
            height: 1.5px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 4px;
            transition: 0.5s all; }
        &_active {
            margin-top: 7px;
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: rotate(+45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader {
        &_phone {
            display: block;
            margin-top: 6px;
            color: #fff;
            font-size: 20px;
            font-weight: 400;
            text-tranform: uppercase;
            text-align: center; }
        &_btn {
            margin-top: 10px;
            display: block;
            width: 200px;
            height: 40px;
            border-radius: 8px;
            margin: 0 auto; } }
    .promo {
        &_header {
            font-size: 35px; } }
    .mobile_item {
        margin: 0 auto;
        margin-top: 30px;
        width: 320px; }
    .title {
        font-size: 30px; }
    .choice {
        min-height: 393px;
        background: none;
        background-color: #070716;
        &_img {
            margin-top: 55px;
            margin-bottom: -50px; }
        &_descr {
            color: #FFFFFF;
            font-weight: 300;
            font-size: 17px; } } }
